import { ReactElement, useEffect, useState } from "react";
import { ILessonMenuItemProps, IScreen } from "../interfaces";

import { CoursewareService } from "../services/CoursewareService";

const coursewareService = new CoursewareService();

const MenuItemLesson = ({
  lesson,
  onSelect,
  currentCourseware,
}: ILessonMenuItemProps): ReactElement => {
  const [screens, setScreens] = useState<IScreen[]>([]);
  const [isSelected, setIsSelected] = useState<boolean>(false);

  const getScreens = async () => {
    if (!isSelected && screens.length === 0) {
      const allScreens: IScreen[] = await coursewareService.getScreens(lesson);

      if (allScreens?.length) {
        setScreens(allScreens);
      }
    }

    setIsSelected(!isSelected);
  };

  const onScreenSelect = (screen: IScreen) => {
    onSelect && onSelect(screen.itemId);
  };

  useEffect(() => {
    if (currentCourseware?.lessonId === lesson.itemId && !isSelected) {
      getScreens();
    }
  }, [currentCourseware]);

  return (
    <li>
      <button className={isSelected ? "expanded" : "collapsed"} onClick={getScreens}>
        {lesson.title}
      </button>

      {screens?.length > 0 && (
        <ul className={!isSelected ? "hidden" : ""} data-testid="screen-list">
          {screens.map((screen: IScreen) => (
            <li key={screen.itemId}>
              <button
                onClick={() => onScreenSelect(screen)}
                className={screen.itemId === currentCourseware?.screenId ? "active" : ""}
              >
                {screen.title}
              </button>
            </li>
          ))}
        </ul>
      )}
    </li>
  );
};

export default MenuItemLesson;
