import { useEffect, useMemo, useRef, useState, Suspense, Fragment } from "react";
import Helmet from "react-helmet";
import "./App.scss";
// @ts-ignore
import CDP from "@pearsontechnology/aero-plugin-api/lib/cdp";
import { IChapter, ICurrentCourseware, ILesson, INavigationItem } from "./interfaces";
import { CoursewareService } from "./services/CoursewareService";
import { learnspaceUrls } from "./utils/constants";
import Toc from "./components/Toc";

// @ts-ignore
window.__CDP = CDP;

const usePrevious = (value: any) => {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  }, [value]);
  return ref.current;
};

const coursewareService = new CoursewareService();

const App = () => {
  const [pluginState, setPluginState] = useState({});
  const contentEl = useRef<HTMLDivElement | null>(null);
  const [elementId, setElementId] = useState("");
  const prevElementId = usePrevious(elementId);
  const [context, setContext] = useState("LEARNER");
  const [nextItem, setNextItem] = useState<INavigationItem | null>(null);
  const [nextItemLoading, setNextItemLoading] = useState(false);
  const [prevItem, setPrevItem] = useState<INavigationItem | null>(null);
  const [prevItemLoading, setPrevItemLoading] = useState(false);
  const [currentCourseware, setCurrentCourseware] = useState<ICurrentCourseware>();
  const [themeCSS, setThemeCSS] = useState("");

  const isNextButtonHidden = useMemo(
    () => !nextItemLoading && !nextItem,
    [nextItem, nextItemLoading],
  );

  const isPrevButtonHidden = useMemo(
    () => !prevItemLoading && !prevItem,
    [prevItem, prevItemLoading],
  );

  const renderFirstScreen = async () => {
    const chapterId = (pluginState as any).chapters[0].itemId;
    const chapter: IChapter = await coursewareService.getStateFromCDP(chapterId);
    if (!chapter.lessons?.length) {
      return;
    }

    const firstLessonId = chapter.lessons[0].itemId;
    const firstLesson: ILesson = await coursewareService.getStateFromCDP(firstLessonId);
    const firstScreenId = firstLesson.pathway[0].itemId;

    if (context === "LEARNER") {
      if (learnspaceUrls.includes(document.referrer)) {
        setElementId(firstScreenId);
      }
    } else {
      onScreenSelect(chapterId, firstLessonId, firstScreenId);
    }
  };

  useEffect(() => {
    const init = async () => {
      try {
        // first register with CDP
        await CDP.register();
        const state = await CDP.state().get();
        setPluginState(state);
        CDP.state().subscribe(setPluginState);
        CDP.route().subscribe(routeHandler);
        CDP.theme().subscribeToCSS(onThemeCSSUpdate);
        const currentContext = await CDP.context().get();
        setContext(currentContext);
      } catch (err) {
        console.error(err);
        console.trace(err);
      }
    };
    init();
  }, []);

  useEffect(() => {
    if (pluginState && Object.keys(pluginState as any).length > 0) {
      renderFirstScreen();
    }
  }, [pluginState, context]);

  useEffect(() => {
    if (elementId) {
      renderContent(elementId);
    }
  }, [elementId]);

  const renderContent = async (id: string) => {
    if (prevElementId) {
      await CDP.child(prevElementId).remove(); // delete the current ID
    }

    await CDP.child(id).init(contentEl.current, {
      context: context === "EDITOR" ? "PREVIEW" : context,
    });
  };

  const routeHandler = (data: any) => {
    if (data.id) {
      // if the screen is from a different lesson (activity)then the activity has to be initialized before changing screen
      setElementId(data.id);
    }
  };

  const onThemeCSSUpdate = (themeCSSstr: string) => {
    if (themeCSS !== themeCSSstr) {
      setThemeCSS(themeCSSstr);
    }
  };

  const onScreenSelect = (chapterId: string, lessonId: string, screenId: string) => {
    setCurrentCourseware({ chapterId, lessonId, screenId });
    setElementId(screenId);

    getNext(chapterId, lessonId, screenId);
    getPrev(chapterId, lessonId, screenId);
  };

  const onNext = () => {
    if (nextItem) {
      onScreenSelect(nextItem.chapterId, nextItem.lessonId, nextItem.screenId);
    }
  };

  const onPrev = () => {
    if (prevItem) {
      onScreenSelect(prevItem.chapterId, prevItem.lessonId, prevItem.screenId);
    }
  };

  const getPrev = async (chapterId: string, lessonId: string, screenId: string) => {
    setPrevItemLoading(true);
    const { pathway } = await coursewareService.getStateFromCDP(lessonId);

    let i = pathway.findIndex((item: any) => item.itemId === screenId) - 1;

    if (i >= 0) {
      const { title } = await coursewareService.getStateFromCDP(pathway[i].itemId || "");
      setPrevItem({
        chapterId,
        lessonId,
        screenId: pathway[i].itemId || "",
        title,
      });

      setPrevItemLoading(false);

      return;
    }

    // First Screen on the Lesson, Go to Prev Lesson
    const { lessons } = await coursewareService.getStateFromCDP(chapterId);
    i = lessons.findIndex((item: any) => item.itemId === lessonId) - 1;

    while (i >= 0) {
      const { pathway } = await coursewareService.getStateFromCDP(lessons[i].itemId);
      if (pathway && pathway.length) {
        const { title } = await coursewareService.getStateFromCDP(
          pathway[pathway.length - 1].itemId,
        );
        setPrevItem({
          chapterId,
          lessonId: lessons[i].itemId,
          screenId: pathway[pathway.length - 1].itemId,
          title,
        });
        setPrevItemLoading(false);
        return;
      }
      i--;
    }

    // First Lesson on the Chapter, Go to the Prev Chapter
    const { chapters } = (pluginState as any) || { chapters: [] };
    i = chapters.findIndex((item: any) => item.itemId === chapterId) - 1;

    while (i >= 0) {
      const { lessons } = await coursewareService.getStateFromCDP(chapters[i].itemId);
      if (lessons && lessons.length) {
        let j;
        for (j = lessons.length - 1; j >= 0; j--) {
          const { pathway } = await coursewareService.getStateFromCDP(lessons[j].itemId);
          if (pathway && pathway.length) {
            const { title } = await coursewareService.getStateFromCDP(
              pathway[pathway.length - 1].itemId,
            );
            setPrevItem({
              chapterId: chapters[i].itemId,
              lessonId: lessons[j].itemId,
              screenId: pathway[pathway.length - 1].itemId,
              title,
            });
            setPrevItemLoading(false);
            return;
          }
        }
      }
      i--;
    }

    setPrevItem(null);
    setPrevItemLoading(false);
  };

  const getNext = async (chapterId: string, lessonId: string, screenId: string) => {
    setNextItemLoading(true);
    const { pathway } = await coursewareService.getStateFromCDP(lessonId);

    let i = pathway.findIndex((item: any) => item.itemId === screenId) + 1;

    if (i < pathway?.length) {
      const { title } = await coursewareService.getStateFromCDP(pathway[i].itemId || "");
      setNextItem({
        chapterId,
        lessonId,
        screenId: pathway[i].itemId || "",
        title,
      });
      setNextItemLoading(false);
      return;
    }

    // Last Screen on the Lesson, Go to Next Lesson
    const { lessons } = await coursewareService.getStateFromCDP(chapterId);
    i = lessons.findIndex((item: any) => item.itemId === lessonId) + 1;

    while (i < lessons.length) {
      const { pathway } = await coursewareService.getStateFromCDP(lessons[i].itemId);
      if (pathway && pathway.length) {
        const { title } = await coursewareService.getStateFromCDP(pathway[0].itemId);
        setNextItem({
          chapterId,
          lessonId: lessons[i].itemId,
          screenId: pathway[0].itemId,
          title,
        });
        setNextItemLoading(false);
        return;
      }
      i++;
    }

    // Last Lesson on the Chapter, Go to the Next Chapter
    const { chapters } = (pluginState as any) || { chapters: [] };
    i = chapters.findIndex((item: any) => item.itemId === chapterId) + 1;

    while (i < chapters.length) {
      const { lessons } = await coursewareService.getStateFromCDP(chapters[i].itemId);
      if (lessons && lessons.length) {
        let j;
        for (j = 0; j < lessons.length; j++) {
          const { pathway } = await coursewareService.getStateFromCDP(lessons[j].itemId);
          if (pathway && pathway.length) {
            const { title } = await coursewareService.getStateFromCDP(pathway[0].itemId);
            setNextItem({
              chapterId: chapters[i].itemId,
              lessonId: lessons[j].itemId,
              screenId: pathway[0].itemId,
              title,
            });
            setNextItemLoading(false);
            return;
          }
        }
      }
      i++;
    }

    setNextItem(null);
    setNextItemLoading(false);
  };

  const renderToc = ({ chapters, title, description, showTOC }: any) => {
    if (chapters === undefined || context === "LEARNER" || context === "" || !showTOC) {
      return null;
    }

    return (
      <Suspense fallback={<div>Suspenseful Loading...</div>}>
        <Toc
          chapters={chapters}
          title={title}
          description={description}
          onSelect={onScreenSelect}
          currentCourseware={currentCourseware as ICurrentCourseware}
        />
      </Suspense>
    );
  };

  return (
    <Fragment>
      <Helmet>{!!themeCSS && <style id="courseThemeCss">{themeCSS}</style>}</Helmet>
      <div className="wrapper">
        {renderToc(pluginState)}

        <div className="container">
          <div id="content-div" className="content" ref={contentEl} />
          {context === "EDITOR" && (
            <Fragment>
              {!isPrevButtonHidden && (
                <button
                  disabled={prevItemLoading}
                  onClick={onPrev}
                  className="navigation-button previous-button"
                >
                  ❮<span> {prevItem?.title}</span>
                </button>
              )}
              {!isNextButtonHidden && (
                <button
                  disabled={nextItemLoading}
                  onClick={onNext}
                  className="navigation-button next-button"
                >
                  <span>{nextItem?.title} </span>❯
                </button>
              )}
            </Fragment>
          )}
        </div>
      </div>
    </Fragment>
  );
};

export default App;
