import { Fragment, ReactElement, useEffect, useState } from "react";

import { ITocProps, IChapter } from "../interfaces";
import MenuItemChapter from "./MenuItemChapter";
import { CoursewareService } from "../services/CoursewareService";

import "./styles.scss";

const coursewareService = new CoursewareService();

const Toc = ({ chapters, title, onSelect, currentCourseware }: ITocProps): ReactElement => {
  const [showToc, setShowToc] = useState<boolean>(false);
  const [chaptersInfo, setChaptersInfo] = useState<IChapter[]>([]);

  useEffect(() => {
    const getChaptersInfo = async () => {
      const allChapters: IChapter[] = await coursewareService.getChapters(chapters);

      if (allChapters?.length > 0) {
        setChaptersInfo(allChapters);
      }
    };

    getChaptersInfo();
  }, []);

  return (
    <Fragment>
      {!showToc && <button className="toc-button" onClick={() => setShowToc(true)}></button>}

      <div className={`toc ${showToc ? "" : "hidden"}`}>
        {chaptersInfo?.length ? (
          <Fragment>
            <button className="close-button" onClick={() => setShowToc(false)}>
              X
            </button>
            <h3>{title}</h3>
            <ul>
              {chaptersInfo.map((chapter: IChapter) => (
                <MenuItemChapter
                  key={chapter.itemId}
                  chapter={chapter}
                  onSelect={(lessonId: string, screenId: string) =>
                    onSelect && onSelect(chapter.itemId, lessonId, screenId)
                  }
                  currentCourseware={currentCourseware}
                />
              ))}
            </ul>
          </Fragment>
        ) : (
          <span>Loading...</span>
        )}
      </div>
    </Fragment>
  );
};

export default Toc;
