import { ReactElement, useEffect, useState } from "react";

import { IChapterMenuItemProps, ILesson } from "../interfaces";
import MenuItemLesson from "./MenuItemLesson";

import { CoursewareService } from "../services/CoursewareService";

const coursewareService = new CoursewareService();

const MenuItemChapter = ({
  chapter,
  onSelect,
  currentCourseware,
}: IChapterMenuItemProps): ReactElement => {
  const [lessons, setLessons] = useState<ILesson[]>([]);
  const [isSelected, setIsSelected] = useState<boolean>(false);

  const getLessons = async () => {
    if (!isSelected && lessons.length === 0) {
      const allLessons: ILesson[] = await coursewareService.getLessons(chapter.lessons);

      if (allLessons?.length) {
        setLessons(allLessons);
      }
    }

    setIsSelected(!isSelected);
  };

  useEffect(() => {
    if (currentCourseware?.chapterId === chapter.itemId && !isSelected) {
      getLessons();
    }
  }, [currentCourseware]);

  return (
    <li>
      <button className={isSelected ? "expanded" : "collapsed"} onClick={getLessons}>
        {chapter.title}
      </button>

      {lessons?.length > 0 && (
        <ul className={!isSelected ? "hidden" : ""} data-testid="lesson-list">
          {lessons.map((lesson: ILesson) => (
            <MenuItemLesson
              key={lesson.itemId}
              lesson={lesson}
              onSelect={(screenId: string) => onSelect && onSelect(lesson.itemId, screenId)}
              currentCourseware={currentCourseware}
            />
          ))}
        </ul>
      )}
    </li>
  );
};

export default MenuItemChapter;
