// @ts-ignore
import CDP from "@pearsontechnology/aero-plugin-api/lib/cdp";

import { IGenericObject, IChapter, ILesson, IScreen } from "../../interfaces";

export class CoursewareService {
  getStateFromCDP = async (coursewareId: string): Promise<any> => {
    return await CDP.courseware(coursewareId).state().get();
  };

  async getChapters(chapters: IGenericObject[]): Promise<IChapter[]> {
    return await Promise.all(
      chapters.map((chapter: IGenericObject) => {
        return this.getStateFromCDP(chapter.itemId).then((data: IChapter) => {
          data.itemId = chapter.itemId;
          return data;
        });
      }),
    );
  }

  async getLessons(lessons: ILesson[]): Promise<ILesson[]> {
    return await Promise.all(
      lessons.map((lesson: ILesson) => {
        return this.getStateFromCDP(lesson.itemId).then((data: ILesson) => {
          data.itemId = lesson.itemId;
          return data;
        });
      }),
    );
  }

  async getScreens(lesson: ILesson): Promise<IScreen[]> {
    const screens: IGenericObject[] = lesson?.pathway;

    return await Promise.all(
      screens.map((screen: IGenericObject) => {
        return this.getStateFromCDP(screen.itemId).then((data: IScreen) => {
          data.itemId = screen.itemId;
          return data;
        });
      }),
    );
  }
}
